.js-edit-mode .o-note {
    -webkit-transform: scale(0.85);
        -ms-transform: scale(0.85);
            transform: scale(0.85);
}

.js-edit-mode .o-note .o-samsung-checkbox {
    -webkit-transform: scale(1.3);
        -ms-transform: scale(1.3);
            transform: scale(1.3);
}

.js-note-edit {
    -webkit-transition: 0.2s all ease;
    -o-transition: 0.2s all ease;
    transition: 0.2s all ease;
    -webkit-transform: scale(1);
        -ms-transform: scale(1);
            transform: scale(1);
    position: absolute;
    right: 0;
}

.js-edit-mode .js-note-edit {
    -webkit-transform: scale(0);
        -ms-transform: scale(0);
            transform: scale(0);
}


/* note modified */

.note-modified {
    font-size: 12px;
    color: rgba(88, 88, 88, 0.54);
    text-align: right;
    margin: 6px;
    display: none;
    padding: 5px 10px;
    font-style: italic;
    font-family: Roboto, 'segoe ui';
}

.o-note.active .note-modified {
    display: inherit;
}

.o-note .editor-category {
    display: none;
}

.o-note.active .editor-category {
    display: inherit;
}


